@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("/fonts/Montserrat-Regular.ttf"), url("/fonts/Montserrat-SemiBold.ttf");
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  font-family: "Montserrat", sans-serif;
  scroll-behavior: smooth;
}

body.fixed {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 85px;
}

a {
  text-decoration: none;
}

p {
  line-height: 1.5;
}

img {
  width: 100%;
  height: auto;
}

li {
  list-style: none;
}

.container {
  width: 70%;
  margin: 0 auto;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;
  background-color: #1f1f42;
}
.header.fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  box-shadow: 0rem 0.1rem 0.3rem #000;
}
.header h1 {
  color: #ffffff;
  text-align: center;
  margin: 0;
}

.nav {
  display: flex;
  flex-direction: row;
  gap: 2rem;
}
.nav a {
  text-align: center;
  color: #ffffff;
  transition: color 0.5s ease;
}
.nav a:hover {
  color: #ffc164;
}

.hero {
  height: 45rem;
  position: relative;
  overflow: hidden;
}

.hero-shadow {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: absolute;
}
.hero-shadow h2 {
  color: #ffffff;
  font-size: 50px;
  margin: 1rem 0;
  text-decoration: underline;
}
.hero-shadow p {
  display: inline-block;
  background-color: #866632;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 700;
  padding: 1rem 1rem;
  border-radius: 1rem;
  user-select: none;
}

.festival {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  padding: 4rem 0;
}

.festival-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.festival-content h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}
.festival-content p {
  margin: 10px 0;
}
.festival-content .date {
  color: #866632;
  font-weight: 700;
}

.lineup {
  background-color: #1f1f42;
  padding: 50px 0;
}
.lineup h3 {
  font-size: 2rem;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 50px 0;
}

.stage-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}
.stage-grid .stage {
  padding-top: 2rem;
  border-radius: 5px;
  box-shadow: 0rem 0.1rem 0.2rem #555555;
}
.stage-grid .stage img {
  margin: 2rem auto 2rem;
  margin: 20px auto 20px;
  width: 150px;
  display: block;
}
.stage-grid p {
  color: #000000;
}

.calendar li {
  padding: 25px;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.background-blue {
  background-color: #9894B2;
}
.background-blue li:nth-child(odd) {
  background-color: #8f8bac;
}

.background-yellow {
  background-color: #ffc164;
}
.background-yellow li:nth-child(odd) {
  background-color: #ffbb55;
}

.gallery {
  padding: 50px 0;
}
.gallery h3 {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
}

.gallery-images {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 15px;
}
.gallery-images img {
  cursor: pointer;
  border-radius: 5px;
  transition: transform 0.3s ease;
}
.gallery-images img:hover {
  transform: scale(1.05);
}

.overlay {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.9);
}
.overlay img {
  display: block;
  width: 70%;
}

.tickets {
  padding: 50px 0;
  background-color: #1f1f42;
}
.tickets h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 20px;
  font-size: 2rem;
}
.tickets .tickets-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
}

.pass {
  color: black;
}
.pass .pass-name {
  text-align: center;
  font-size: 25px;
  color: #ffffff;
  margin-bottom: 20px;
  font-weight: bold;
}
.pass-header, .pass-footer {
  padding: 0.5rem;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.pass-header {
  background-color: #866632;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 16px;
}
.pass-body {
  background-color: #ffffff;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 13px;
  border-bottom: 1px solid #e1e1e1;
  text-align: center;
}
.pass-footer {
  background-color: #866632;
}
.pass-footer p {
  font-size: 25px;
  font-weight: 700;
}

.footer {
  background-color: #15152b;
  padding: 3rem;
}
.footer p {
  font-size: 1.4rem;
  font-weight: 700;
  color: #a7a0ba;
  text-align: center;
}

@media only screen and (width <= 992px) {
  .container {
    width: 90%;
  }
  .festival {
    grid-template-columns: auto;
  }
  .gallery-images {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media only screen and (width <= 576px) {
  .container {
    width: 90%;
  }
  .header {
    display: flex;
    flex-direction: column;
  }
  .header h1 {
    margin-bottom: 2rem;
  }
  .header.fixed {
    position: absolute;
  }
  .nav {
    flex-direction: column;
    gap: 1.5rem;
  }
  .hero-shadow h2 {
    font-size: 2rem;
    text-align: center;
  }
  .hero-shadow p {
    font-size: 1.2rem;
    padding: 10px 15px;
  }
  .stage-grid {
    grid-template-columns: auto;
  }
  .gallery-images {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
  .overlay img {
    width: 100%;
  }
  .tickets .tickets-grid {
    grid-template-columns: auto;
    gap: 2rem;
  }
  .footer p {
    font-size: 1.2rem;
  }
}